import React from 'react'
import './App.css'
import { Hintput } from './Hintput'
const names = [
  'Messi',
  'Cristiano Ronaldo',
  'Roberto Ronaldo',
  'Zin√©dine Zidane',
  'Ronaldo',
  'Muhammad Salah',
  'Muhammad Ali',
  'Jamie Ali',
  'ali javid',
  'Salah Abdul',
  'abdullah',
  'michael Jeffrey jordan',
  'Reilly Wynn',
  'Sarah-Jane Singh',
  'Luna Soto',
  'Samual Samuels',
  'Shanna Holder',
  'Esme-Rose Steadman',
]
function App() {
  const [name, setName] = React.useState('')
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setName(e.target.value)
  }
  return (
    <div>
      <pre style={{ backgroundColor: '#eee', padding: '1.3em', width: '30%' }}>
        {names.map((n, i) => (
          <p key={i} style={{ color: 'black' }}>
            {n}
          </p>
        ))}
      </pre>
      <p>
        <Hintput
          items={names}
          name="names"
          handleBlur={handleChange}
          handleChange={handleChange}
          placeholder="Type a name"
        />
      </p>
      <p style={{ color: 'green' }}>{name}</p>
      <p>
        <input type="text" className="hint" placeholder="regular input" />
      </p>
    </div>
  )
}

export default App
